import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanIODiagnosticsInstanceResult} from "@generated/vsan-i-o-diagnostics-instance-result";
import {DurationPickerState} from "@component/datetime-picker/time-last-field-picker/last-field-date-picker.component";
import {DateRangeData} from "@component/datetime-picker/date-range-picker.component";
import {VsanIODiagnosticsInstance} from "@generated/vsan-i-o-diagnostics-instance";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {LastFieldDatePickerUtil} from "@component/datetime-picker/time-last-field-picker/last-field-date-picker-util";
import {ManagedObject} from "@service/managed-object";

@Component({
   templateUrl: "io-diagnostics-instances.html",
   styleUrls: ["io-diagnostics-instances.scss"],
   selector: "vsan-io-diagnostics-instances",
})
export class IoDiagnosticsInstancesComponent {

   failedChecksReasons: string[];
   instances: VsanIODiagnosticsInstance[];
   clsRef: ManagedObject;

   @Input()
   set ioInstanceResult(value: VsanIODiagnosticsInstanceResult) {
      if (value) {
         this.failedChecksReasons = value.precheckResult?.failedChecks.map(failedCheck => failedCheck.reason);
         this.instances = value.instances;
         this.clsRef = value.clsRef;
         this.filterInstances();
      }
   }

   filteredInstances: VsanIODiagnosticsInstance[] = [];

   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;

   timePickerRange: DateRangeData;

   @Input()
   timePickerState: DurationPickerState = DurationPickerState.ALL;

   @Output()
   timePickerStateChange: EventEmitter<DurationPickerState> = new EventEmitter<DurationPickerState>();

   onTimeRangeChange(timeRange: DateRangeData): void {
      this.timePickerRange = timeRange;
      this.filterInstances();
   }

   onPickerStateChange(timePickerState: DurationPickerState): void {
      this.timePickerStateChange.emit(timePickerState);
   }

   private filterInstances(): void {
      this.filteredInstances = LastFieldDatePickerUtil.filterItemsByTimeRange(this.timePickerRange,
                                                                              this.timePickerState,
                                                                              this.instances);
   }
}