import {Injectable} from "@angular/core";
import {Observable, Subject} from "rxjs";

/**
 * A service that manages communication between instance, instance list and main view.
 */
@Injectable()
export class IoDiagnosticsInstanceService {
   private statsQueried: Subject<string> = new Subject<string>();
   onStatsQueried: Observable<string> = this.statsQueried.asObservable();

   private instanceListQueried: Subject<void> = new Subject<void>();
   onInstanceListQueried: Observable<void> = this.instanceListQueried.asObservable();

   private instanceListViewEnabled: Subject<void> = new Subject<void>();
   onInstanceListViewEnabled: Observable<void> = this.instanceListViewEnabled.asObservable();

   private vmdkIdSelected: Subject<string> = new Subject<string>();
   onVmdkIdSelected: Observable<string> = this.vmdkIdSelected.asObservable();

   private errorThrown: Subject<string> = new Subject<string>();
   onErrorThrown: Observable<string> = this.errorThrown.asObservable();

   queryStats(instanceName: string): void {
      this.statsQueried.next(instanceName);
   }

   queryInstanceList(): void {
      this.instanceListQueried.next();
   }

   enableInstanceListView(): void {
      this.instanceListViewEnabled.next();
   }

   selectVmdkId(vmdkId: string): void {
      this.vmdkIdSelected.next(vmdkId);
   }

   throwError(errorMsg: string): void {
      this.errorThrown.next(errorMsg);
   }
}