export class CytoscapeLayouts {
   static DAGRE: DagreLayoutOptions = {
      name: "dagre",
      rankDir: "LR",
      fit: false,
      // This is required to handle missing edge when a node has a longer label and using bezier curves:
      // https://github.com/cytoscape/cytoscape.js/issues/2884
      rankSep: 80,
   };
}

interface DagreLayoutOptions {
   name: string; // Value must be "dagre";
   // Dagre algo options, uses default value on undefined
   nodeSep?: number; // Default undefined. The separation between adjacent nodes in the same rank
   edgeSep?: number; // Default undefined. The separation between adjacent edges in the same rank
   rankSep?: number; // Default undefined. The separation between each rank in the layout
   rankDir?: string; // Default undefined. 'TB' for top to bottom flow, 'LR' for left to right,
   ranker?: string; // Default undefined. Tpe of algorithm to assign a rank to each node in the input graph.
   // Possible values: 'network-simplex', 'tight-tree' or 'longest-path'
   minLen?: (edge) => number; //  Default 1. Number of ranks to keep between the source and target of the edge
   edgeWeight?: (edge) => number; // Default 1. Higher weight edges are generally made shorter and
   // straighter than lower weight edges

   // General layout options
   fit?: boolean; // Default true. Whether to fit to viewport
   padding?: number; // Default 30. Fit padding
   spacingFactor?: number; // Default undefined. Applies a multiplicative factor (>0) to expand or compress the
   // overall area that the nodes take up
   nodeDimensionsIncludeLabels?: boolean; // Default false. Whether labels should be included in
   // determining the space used by a node
   animate?: boolean; // Default false. whether to transition the node positions
   animateFilter?: (node, i) => boolean; // Default true.  Whether to animate specific nodes when animation is on; n
   // on-animated nodes immediately go to their final positions
   animationDuration?: number; // Default 500. Duration of animation in ms if enabled
   animationEasing?: boolean; // Default undefined. Easing of animation if enabled
   boundingBox?: any; // Default undefined. Constrain layout bounds; { x1, y1, x2, y2 } or { x1, y1, w, h }
   transform?: (node, pos) => any; // A function that applies a transform to the final node position
   ready?: () => void; // On layoutready
   stop?: () => void; // On layoutstop
}