import {Injectable} from "@angular/core";
import {GraphClasses} from "@generated/graph-classes";
import {MapUtil} from "@util/map-util";
import cytoscape, {
   BoundingBox12,
   BoundingBoxWH,
   Collection,
   EdgeCollection,
   EdgeSingular,
   NodeCollection,
   NodeSingular,
   SearchFirstOptions,
   SingularElementArgument,
} from "cytoscape";
import {IoDiagnosticsUtil} from "../io-diagnostics.util";
import {CytoscapeLayouts} from "./cytoscape-properties/cytoscape-layouts";
import {GraphLayoutSpec} from "./model/graph-layout-spec";

/**
 * A service that adjusts the graph layout (i.e., handles compound nodes overlapping, left aligns nodes in columns,
 * creates bezier edges, resizes graph's container and viewport as per graph elements' rendered boundingbox).
 */
@Injectable()
export class GraphLayoutService {

   private static readonly COMPOUND_NODE_MARGIN: number = 30;
   private static readonly RAID_NODES_MAX_VERTICAL_DISTANCE: number = 40;
   private static readonly RAID_STORAGE_NODE_VERTICAL_MARGIN: number = 50;
   private static readonly RAID_STORAGE_NODE_HORIZONTAL_MARGIN: number = 40;

   /**
    * Method that runs custom layout modifications.
    * Method execution order matters!
    */
   run(graph: cytoscape.Core, spec: GraphLayoutSpec): void {
      const raidNodes: NodeCollection = this.getStackedRaidNodes(graph);
      // vSAN2 check - horizontal alignment of domowner storage nodes is enabled for vSAN2 only.
      if (raidNodes.length !== 2) {
         spec.horizontallyAlignDomOwnerStorageNodes = false;
      }
      if (spec.runDagre) {
         const graphLayoutElements: Collection = this.getGraphElements(graph,
               spec.horizontallyAlignDomOwnerStorageNodes);
         graphLayoutElements.layout(CytoscapeLayouts.DAGRE).run();
      }
      if (spec.horizontallyAlignDomOwnerStorageNodes) {
         this.horizontallyAlignDomOwnerStorageNodes(graph);
      }
      if (spec.offsetOverlappingCompoundNodes) {
         this.offsetOverlappingCompoundNodes(graph);
      }
      if (spec.leftCenterNodes) {
         this.leftCenterNodes(graph, spec.horizontallyAlignDomOwnerStorageNodes);
      }
      if (spec.createBezierCurves) {
         this.createBezierCurves(graph);
      }
      if (spec.resizeViewport) {
         this.resizeViewport(graph);
      }
   }

   /**
    * Get raid nodes sorted by y-position. For example uppermost element is the last element from the array.
    * Method must be invoked each time cytoscape's layout is run in order to get actual/valid nodes.
    */
   private getStackedRaidNodes(graph: cytoscape.Core): NodeCollection {
      const raidNodes: NodeCollection = graph.nodes()
            .filter(n => n.hasClass(GraphClasses.RAID_NODE()))
            .sort((n1, n2) => (n1.renderedBoundingBox({}).y1 > n2.renderedBoundingBox({}).y1)
                  ? 1
                  : -1);
      return raidNodes;
   }

   getGraphElements(graph: cytoscape.Core, horizontallyAlignDomOwnerStorageNodes: boolean): Collection {
      return horizontallyAlignDomOwnerStorageNodes
            ? this.filterDomOwnerStorageNodes(graph)
            : graph.elements();
   }

   /**
    *  Return graph nodes and edges excluding storage nodes (and related edges) in dom owner.
    *  Excluded nodes are horizontally aligned later in horizontallyAlignDomOwnerStorageNodes
    */
   private filterDomOwnerStorageNodes(graph: cytoscape.Core): Collection {
      const domOwnerId: string = graph.nodes().filter(node =>
            this.hasClass(node, GraphClasses.DOM_OWNER_NODE())).first().data().id;
      const domOwnerStorageNodeIds: string[] = [];
      const nonDomOwnerNodes: NodeCollection = graph.nodes().filter(node => {
         const isComputeHostDisk: boolean = this.isComputeHostStorageNode(node, domOwnerId);
         if (isComputeHostDisk) {
            domOwnerStorageNodeIds.push(node.data().id);
            return false;
         } else {
            return true;
         }
      });

      const edgesNotToDomOwnerDisks: EdgeCollection = graph.edges()
            .filter(edge => !domOwnerStorageNodeIds.some(diskNodeId => diskNodeId === edge.data().target));
      const result: Collection = nonDomOwnerNodes.add(edgesNotToDomOwnerDisks);
      return result;
   }

   private isComputeHostStorageNode(node: NodeSingular, domOwnerId: string): boolean {
      return node.data().parent === domOwnerId && this.hasClass(node, GraphClasses.STORAGE_NODE());
   }

   private hasClass(node: NodeSingular, className: string): boolean {
      return node.classes().some(cssClass => (cssClass as unknown as string) === className);
   }

   /**
    * Align horizontally storage nodes inside domowner parent node. Applied for vSAN2 only.
    */
   horizontallyAlignDomOwnerStorageNodes = (graph: cytoscape.Core): void => {
      const raidNodes: NodeCollection = this.getStackedRaidNodes(graph);
      // First approach raid nodes vertically if they are too far away from each other.
      this.ensureRaidNodesMaxVerticalDistance(raidNodes, GraphLayoutService.RAID_NODES_MAX_VERTICAL_DISTANCE);
      // Then horizontally align storage nodes from the outer sides of raid nodes.
      this.horizontallyAlignNodes(raidNodes[0], true);
      this.horizontallyAlignNodes(raidNodes[1], false);
   };

   private ensureRaidNodesMaxVerticalDistance(raidNodes: NodeCollection, maxDistance: number): void {
      const topRaidNode: NodeSingular = raidNodes[0];
      const bottomRaidNode: NodeSingular = raidNodes[1];
      if (raidNodes.length !== 2) {
         return;
      }
      const topNodeYPos: number = topRaidNode.renderedBoundingBox({}).y2;
      const bottomNodeYPos: number = bottomRaidNode.renderedBoundingBox({}).y1;
      const raidNodesVerticalDistance: number = bottomNodeYPos - topNodeYPos;
      if (raidNodesVerticalDistance <= maxDistance) {
         return;
      }
      const nodeVerticalDiff: number = (maxDistance - raidNodesVerticalDistance) / 2;
      topRaidNode.position({
         x: topRaidNode.position().x,
         y: topRaidNode.position().y - nodeVerticalDiff,
      });
      bottomRaidNode.position({
         x: bottomRaidNode.position().x,
         y: bottomRaidNode.position().y + nodeVerticalDiff,
      });
   }

   private horizontallyAlignNodes(raidNode: NodeSingular, isUpper: boolean): void {
      const raidNodeBbox: BoundingBox12 = raidNode.renderedBoundingBox({});
      const storageNodeSelector: string = `node.${GraphClasses.STORAGE_NODE()}`;
      const storageNodes: NodeCollection = raidNode.outgoers(storageNodeSelector);
      const storageNodeYPos: number = isUpper
            ? raidNodeBbox.y1 - GraphLayoutService.RAID_STORAGE_NODE_VERTICAL_MARGIN
            : raidNodeBbox.y2 + GraphLayoutService.RAID_STORAGE_NODE_VERTICAL_MARGIN;
      let storageNodeXPos: number = raidNodeBbox.x2;
      for (let i: number = 0; i < storageNodes.length; i++) {
         if (i !== 0) {
            const previousNodeWidth: number = storageNodes[i - 1].renderedBoundingBox({}).w;
            storageNodeXPos =
                  storageNodeXPos - previousNodeWidth - GraphLayoutService.RAID_STORAGE_NODE_HORIZONTAL_MARGIN;
         }
         storageNodes[i].position({
            x: storageNodeXPos - storageNodes[i].renderedBoundingBox({}).w / 2,
            y: storageNodeYPos,
         });
      }
   }

   /**
    * Offset overlapping compound nodes with domowner compound node
    * This happens when domowner doesn't have disk groups.
    */
   offsetOverlappingCompoundNodes = (graph: cytoscape.Core): void => {
      const compoundNodes: NodeCollection = graph.nodes()
            .filter(node => node.isParent() && !IoDiagnosticsUtil.isDomOwnerNode(node));
      const domOwner: SingularElementArgument = graph.nodes()
            .filter(node => node.isParent() && IoDiagnosticsUtil.isDomOwnerNode(node))[0];
      const domOwnerBbox: BoundingBox12 & BoundingBoxWH = domOwner.renderedBoundingBox({});
      compoundNodes.forEach(compoundNode => {
         const compoundNodeBbox: BoundingBox12 & BoundingBoxWH = compoundNode.renderedBoundingBox({});
         const offsetX: number = this.getCompoundNodeOffsetX(domOwnerBbox, compoundNodeBbox);
         if (offsetX) {
            compoundNode.position({
               x: compoundNode.position().x + offsetX,
               y: compoundNode.position().y,
            });
         }
      });
   };

   private getCompoundNodeOffsetX(leftBbox: BoundingBox12 & BoundingBoxWH,
         rightBbox: BoundingBox12 & BoundingBoxWH): number {
      const bboxPositionDiff: number = rightBbox.x1 - leftBbox.x2;
      const isXIntersected: boolean = bboxPositionDiff < GraphLayoutService.COMPOUND_NODE_MARGIN;
      return isXIntersected
            ? GraphLayoutService.COMPOUND_NODE_MARGIN - bboxPositionDiff
            : null;
   }

   private rankToNodes: Map<string, NodeSingular[]>;
   /**
    * Left-center nodes according to the leftmost positioned node in a rank
    */
   leftCenterNodes = (graph: cytoscape.Core, horizontallyAlignDomOwnerStorageNodes: boolean): void => {
      this.rankToNodes = new Map();
      const eles: Collection = this.getGraphElements(graph, horizontallyAlignDomOwnerStorageNodes);
      eles.bfs(this.traverseOptions);
      this.rankToNodes.forEach(rankNodes => this.offsetNodesInRank(rankNodes));
   };

   private nodeVisit = (currentNode: NodeSingular, currPrevNodeEdge: EdgeSingular,
         previousNode: NodeSingular, visitedNodeIndex: number, depth: number): void => {
      MapUtil.addArrayValue(this.rankToNodes, depth, currentNode);
   };

   private traverseOptions: SearchFirstOptions = {
      roots: `.${GraphClasses.ROOT_NODE()}`,
      visit: this.nodeVisit,
      directed: false,
   };

   private offsetNodesInRank = (rankNodes: NodeSingular[]): void => {
      const x1Min: number = Math.min(...rankNodes.map(node => node.renderedBoundingBox({}).x1));
      rankNodes.forEach(node => {
         const boundingBox: BoundingBox12 = node.renderedBoundingBox({});
         const offsetX: number = boundingBox.x1 - x1Min;
         node.position({
            x: node.position().x - offsetX,
            y: node.position().y,
         });
      });
   };

   createBezierCurves = (graph: cytoscape.Core): void => {
      graph.edges().forEach(edge => {
         const controlPoint: ControlPoint = this.calcBezierPoints(edge);
         edge.style("control-point-distances", controlPoint.distances);
         edge.style("control-point-weights", controlPoint.weights);
      });
   };

   private calcBezierPoints = (edge: EdgeSingular): ControlPoint => {
      const d: Distances = this.getDistances(edge);
      const cosTheta: number = d.x / d.z;
      const alpha: number = 0.25;
      const controlPointWeights: [number, number] = [alpha, 1 - alpha];
      const controlPointDistances: [number, number] = [-alpha * d.y * cosTheta, alpha * d.y * cosTheta];
      return {
         weights: controlPointWeights,
         distances: controlPointDistances,
      };
   };

   private getDistances = (edge: EdgeSingular): Distances => {
      const x0: number = edge.source().position("x");
      const x1: number = edge.target().position("x");
      const y0: number = edge.source().position("y");
      const y1: number = edge.target().position("y");
      const x: number = x1 - x0;
      const y: number = y1 - y0;
      const z: number = Math.sqrt(x * x + y * y);
      return {
         x: x,
         y: y,
         z: z,
      };
   };

   /**
    * Resize graph's container and viewport as per graph elements' rendered boundingbox.
    */
   resizeViewport(graph: cytoscape.Core): void {
      const renderedBoundingbox: BoundingBox12 & BoundingBoxWH = graph.elements().renderedBoundingbox(null);
      graph.container().style.height = `${renderedBoundingbox.h.toString()}px`;
      graph.container().style.width = `${renderedBoundingbox.w.toString()}px`;
      graph.resize();
      graph.fit();
   }
}

export interface Distances {
   x: number;
   y: number;
   z: number;
}

export interface ControlPoint {
   weights: [number, number];
   distances: [number, number];
}