import {Icon} from "@util/icon";
import {StringUtil, TextStyles} from "@util/string-util";
import {VsanUiUtils} from "@util/vsan-util";
import {NodeSingular} from "cytoscape";
import {IoDiagnosticsUtil} from "../../io-diagnostics.util";
import {ParentNodeImageColors} from "../model/parent-node-image-colors";
import {GraphConstants} from "./graph-constants";

/**
 * Utility class that draws SVG images (e.g., icon, line and text).
 * SVG images are added as background images in compound nodes.
 */
export class SvgUtil {
   static SVG_DATA_SCHEME: string = "data:image/svg+xml;utf8,";
   static FONT_SIZE: number = GraphConstants.FONT_SIZE;
   static FONT_FAMILY: string = StringUtil.FONT_FAMILY;
   static BADGE_VERTICAL_PADDING: number = 4;
   static BADGE_HORIZONTAL_PADDING: number = 10;
   static BADGE_BORDER_SIZE: number = 1;

   static getParentNodeImages(node: NodeSingular,
         iconSize: number,
         parentNodeImageColors: ParentNodeImageColors): string[] {
      const images: string[] = [];
      images.push(SvgUtil.getHostImage(iconSize, parentNodeImageColors.color));
      images.push(SvgUtil.getText(node.data().label, parentNodeImageColors.color));
      if (IoDiagnosticsUtil.isDomOwnerNode(node)) {
         images.push(SvgUtil.getBadge(SvgUtil.getBadgeLabel(), parentNodeImageColors));
      }
      return images;
   }

   static getBadgeLabel(): string {
      return VsanUiUtils.getString("vsan.monitor.vm.iodiagnostics.graph.node.domOwner.label");
   }

   /**
    * Method that returns Y positions of SVG elements(i.e., Icon, Icon text and badge label) in the parent nodes
    */
   static getParentNodeImagesYpos(node: NodeSingular, iconYpos: number, iconSize: number): number[] {
      const imagesYpos: number[] = [];
      imagesYpos.push(iconYpos); // Icon Y position
      const iconTextYPos: number = iconYpos + iconSize / 2 - SvgUtil.FONT_SIZE / 2 + 1;
      imagesYpos.push(iconTextYPos); // Icon text Y position
      if (IoDiagnosticsUtil.isDomOwnerNode(node)) {
         imagesYpos.push(iconYpos - SvgUtil.BADGE_VERTICAL_PADDING); // Badge Y position
      }
      return imagesYpos;
   }

   /**
    * Method that returns X positions of SVG elements(i.e., Icon, Icon text and badge label) in the parent nodes
    */
   static getParentNodeImagesDimensionsX(node: NodeSingular,
         iconXpos: number,
         iconSize: number,
         leftMargin: number): ImageDimensionsX {
      const imageDimensionsX: ImageDimensionsX = new ImageDimensionsX();
      imageDimensionsX.positions.push(iconXpos); // Icon X position
      imageDimensionsX.widths.push(iconSize); // Icon width
      const textXpos: number = iconXpos + iconSize + leftMargin;
      imageDimensionsX.positions.push(textXpos); // Icon text X position
      const textStyles: TextStyles = {
         size: SvgUtil.FONT_SIZE,
         family: SvgUtil.FONT_FAMILY as string,
      };
      const textWidth: number = StringUtil.getTextWidth(node.data().label, textStyles);
      imageDimensionsX.widths.push(textWidth); // Text width
      if (IoDiagnosticsUtil.isDomOwnerNode(node)) {
         const badgeXpos: number = textXpos + textWidth + leftMargin;
         imageDimensionsX.positions.push(badgeXpos); // Badge X position
         const labelD: LabelDimensions = LabelDimensions.fromLabel(SvgUtil.getBadgeLabel());
         const rectD: RectDimensions = RectDimensions.fromLabelDimensions(labelD);
         const svgD: SvgDimensions = SvgDimensions.fromRectDimensions(rectD);
         imageDimensionsX.widths.push(svgD.width); // Badge width
      }
      return imageDimensionsX;
   }

   private static getText = (text: string, color: string, itemWidth?: number,
         fontSize: number = SvgUtil.FONT_SIZE, fontFamily: string = SvgUtil.FONT_FAMILY): string => {
      const yPosition: number = fontSize / 2;
      if (VsanUiUtils.isUnset(itemWidth)) {
         itemWidth = StringUtil.getTextWidth(text, {
            size: fontSize,
            family: fontFamily,
         });
      }
      const textStyle: Map<string, string> = new Map()
            .set("fill", color)
            .set("font-size", `${fontSize}`)
            .set("font-family", fontFamily)
            .set("dominant-baseline", "middle");
      const svgText: string = SvgUtil.createSvgText(text, 0, yPosition, textStyle);
      const svgString: string = SvgUtil.getSvgStr(svgText, itemWidth, fontSize);
      return svgString;
   };

   private static getSvgStr = (svgText: string, viewPortWidth: number, viewPortHeight: number)
         : string => {
      const svgEl: HTMLElement = SvgUtil.createSvg(svgText, viewPortWidth, viewPortHeight);
      return `${SvgUtil.SVG_DATA_SCHEME}${encodeURIComponent(svgEl.outerHTML)}`;
   };

   private static createSvg = (svgStr: string, width: number = 100, height: number = 100)
         : HTMLElement => {
      const svgText: string = `<?xml version="1.0" encoding="UTF-8"?><!DOCTYPE svg>
            <svg xmlns='http://www.w3.org/2000/svg' version='1.1' width='${width}' height='${height}'>${svgStr}</svg>`;
      return SvgUtil.getDocElementFromString(svgText);
   };

   private static getDocElementFromString(svgText: string): HTMLElement {
      const parser: DOMParser = new DOMParser();
      return parser.parseFromString(svgText, "text/xml").documentElement;
   }

   private static createSvgText = (text: string, xPos: number, yPos: number, styleMap: Map<string, string>): string => {
      return `<text x='${xPos}' y='${yPos}' ${SvgUtil.convertStyleMapToString(styleMap)}>${text}</text>`;
   };

   /**
    * Coverts Map object's entries that store styling (i.e., key: property name, value: property value) to string.
    */
   private static convertStyleMapToString = (styleMap: Map<string, string>): string => {
      if (!styleMap) {
         return "";
      }
      let s: string = "";
      for (const [k, v] of styleMap) {
         s += `${k}="${v}" `;
      }
      return s;
   };

   private static getHostImage(iconSize: number, color: string): string {
      const hostImgString: string = (window as any).ClarityIcons.get(Icon.HOST);
      const hostImgDoc: HTMLElement = SvgUtil.getDocElementFromString(hostImgString);
      SvgUtil.updateHostImageColor(hostImgDoc, color);
      hostImgDoc.setAttribute("width", iconSize.toString());
      hostImgDoc.setAttribute("height", iconSize.toString());
      const svgString: string = `${SvgUtil.SVG_DATA_SCHEME}${encodeURIComponent(hostImgDoc.outerHTML)}`;
      return svgString;
   }

   private static updateHostImageColor(hostImgDoc: HTMLElement, color: string): void {
      const svgDefs: HTMLCollectionOf<SVGDefsElement> = hostImgDoc.getElementsByTagName("defs");
      const hasStyles: boolean = !!(svgDefs?.length && svgDefs[0].children?.length);
      if (hasStyles) {
         const svgStyles: Element = svgDefs[0].children[0];
         const vmwStyle: string = svgStyles.innerHTML;
         const startIndex: number = vmwStyle.indexOf(".vmw-color-theme{");
         if (startIndex > -1) {
            const endIndex: number = vmwStyle.indexOf("}", startIndex);
            const newStyle: string = `.vmw-color-theme{fill:${color}`;
            const updatedStyles: string =
                  `${vmwStyle.substring(0, startIndex)}${newStyle}${vmwStyle.substring(endIndex)}`;
            svgStyles.innerHTML = updatedStyles;
         }
      }
   }

   private static getBadge(label: string, parentNodeImageColors: ParentNodeImageColors): string {
      const rectFillColor: string = parentNodeImageColors.badgeBackground;
      const rectStrokeColor: string = parentNodeImageColors.badgeBorder;
      const textColor: string = parentNodeImageColors.badgeColor;

      const labelD: LabelDimensions = LabelDimensions.fromLabel(label);
      const rectD: RectDimensions = RectDimensions.fromLabelDimensions(labelD);
      const svgD: SvgDimensions = SvgDimensions.fromRectDimensions(rectD);

      const labelYposOffset: number = 3;
      const labelYpos: number = (svgD.height / 2) + (labelD.fontSize / 2) - labelYposOffset;
      const labelXpos: number = labelD.horizontalPadding + rectD.xPos;

      const textStyle: Map<string, string> = new Map()
            .set("fill", textColor)
            .set("font-size", `${labelD.fontSize}px`)
            .set("font-family", labelD.fontFamily);
      const svgText: string = SvgUtil.createSvgText(label, labelXpos, labelYpos, textStyle);

      const svgContent: string = `<rect x="${rectD.xPos}" y="${rectD.yPos}"
width="${rectD.width}" height="${rectD.height}"
fill="${rectFillColor}" stroke="${rectStrokeColor}" stroke-width="${rectD.borderSize}"
ry="10" rx="10"/>${svgText}`;
      const svgString: string = SvgUtil.getSvgStr(svgContent, svgD.width, svgD.height);
      return svgString;
   }
}

class LabelDimensions {
   constructor(public fontFamily?: string,
         public fontSize?: number,
         public horizontalPadding?: number,
         public verticalPadding?: number,
         public width?: number) {
   }

   static fromLabel(label: string): LabelDimensions {
      const result: LabelDimensions = new LabelDimensions();
      result.fontFamily = SvgUtil.FONT_FAMILY;
      result.fontSize = SvgUtil.FONT_SIZE;
      result.horizontalPadding = SvgUtil.BADGE_HORIZONTAL_PADDING;
      result.verticalPadding = SvgUtil.BADGE_VERTICAL_PADDING;
      result.width = StringUtil.getTextWidth(label, {
         family: result.fontFamily,
         size: result.fontSize,
      });
      return result;
   }
}

class RectDimensions {
   constructor(public width?: number,
         public height?: number,
         public borderSize?: number,
         public offsetPos?: number,
         public xPos?: number,
         public yPos?: number) {
   }

   static fromLabelDimensions(labelD: LabelDimensions): RectDimensions {
      const rect: RectDimensions = new RectDimensions();
      rect.width = labelD.width + labelD.horizontalPadding * 2;
      rect.height = labelD.fontSize + labelD.verticalPadding * 2;
      rect.borderSize = SvgUtil.BADGE_BORDER_SIZE;
      rect.offsetPos = 1;
      rect.xPos = rect.borderSize + rect.offsetPos;
      rect.yPos = rect.borderSize;
      return rect;
   }
}

class SvgDimensions {
   constructor(public width?: number,
         public height?: number) {
   }

   static fromRectDimensions(rectD: RectDimensions): SvgDimensions {
      const width: number = rectD.width + rectD.xPos * 2 + 10;
      const height: number = rectD.height + rectD.yPos * 2;
      return new SvgDimensions(width, height);
   }
}

export class ImageDimensionsX {
   /**
    * X positions of images for aligning them over compound node.
    */
   positions: number[] = [];
   /**
    * The widths of images, aligned over the compound node.
    */
   widths: number[] = [];

   getFullWidth(): number {
      // Redundant must be removed because of an issue - https://github.com/cytoscape/cytoscape.js/issues/2885
      const REDUNDANT_SPACE: number = 83;
      const offset: number = GraphConstants.NODE_IMAGE_LEFT_MARGIN - REDUNDANT_SPACE;
      return this.positions[this.positions.length - 1] + this.widths[this.widths.length - 1] + offset;
   }
}
