import {Component, Input} from "@angular/core";
import {VsanIODiagnosticsInstance} from "@generated/vsan-i-o-diagnostics-instance";
import {IoDiagnosticsInstanceService} from "./io-diagnostics-instance.service";
import {VsanUiUtils} from "@util/vsan-util";
import {DateFormat, VsanDateFormatterPipe} from "@pipe/VsanDateFormatterPipe";
import {Logger} from "@util/logger";
import {VsanIODiagnosticsInstanceState} from "@generated/vsan-i-o-diagnostics-instance-state";

@Component({
   templateUrl: "io-diagnostics-instance-card.html",
   styleUrls: ["io-diagnostics-instance-card.scss"],
   selector: "vsan-io-diagnostics-instance-card",
})
export class IoDiagnosticsInstanceCardComponent {
   private static readonly DATE_FORMATTER: VsanDateFormatterPipe = new VsanDateFormatterPipe();

   @Input()
   ioInstance: VsanIODiagnosticsInstance;

   constructor(private ioDiagnosticsInstanceService: IoDiagnosticsInstanceService) {
   }

   isRunningInstance(): boolean {
      return this.ioInstance?.state === VsanIODiagnosticsInstanceState.RUNNING;
   }

   isCompletedInstance(): boolean {
      return this.ioInstance?.state === VsanIODiagnosticsInstanceState.COMPLETED;
   }

   queryStats(): void {
      this.ioDiagnosticsInstanceService.queryStats(this.ioInstance.name);
   }

   getDurationLabel(): string {
      if (this.ioInstance) {
         return VsanUiUtils.getString("vsan.monitor.vm.iodiagnostics.instance.duration",
               IoDiagnosticsInstanceCardComponent.DATE_FORMATTER.transform(this.ioInstance.timeRange.startTime,
                                                                           DateFormat.NO_SECONDS),
               IoDiagnosticsInstanceCardComponent.DATE_FORMATTER.transform(this.ioInstance.timeRange.endTime,
                                                                           DateFormat.NO_SECONDS),
               this.ioInstance.timeRange.duration);
      }
   }

   getAlertStyle(): string {
      if (this.isCompletedInstance()) {
         return this.hasEvents() ? "label-warning" : "label-success";
      }
      return "label-info";
   }

   getStatusLabel(): string {
      switch (this.ioInstance?.state) {
         case VsanIODiagnosticsInstanceState.COMPLETED:
            const localizationKey: string = this.hasEvents() ?
                  "vsan.monitor.vm.iodiagnostics.instance.state.completed.events" :
                  "vsan.monitor.vm.iodiagnostics.instance.state.completed";
            return VsanUiUtils.getString(localizationKey);
         case VsanIODiagnosticsInstanceState.RUNNING:
            return VsanUiUtils.getString("vsan.monitor.vm.iodiagnostics.instance.state.running",
                  this.ioInstance.timeRange.remainingTimeLocalizedTimeUnit);
         default:
            Logger.warn("Unrecognized io diagnostics instance status:", this.ioInstance?.state);
            return VsanUiUtils.getString("vsan.na.label");
      }
   }

   private hasEvents(): boolean {
      return !!this.ioInstance?.events?.length;
   }
}