import {Injectable} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {GraphConstants} from "./graph-constants";
import {StringUtil} from "@util/string-util";
import {GraphThemeService} from "../graph-theme.service";
import {ImageDimensionsX, SvgUtil} from "./svg-util";
import {GraphClasses} from "@generated/graph-classes";
import {GraphElementStyles} from "../model/graph-element-styles";

/**
 * The service provides styling for Cytoscape diagram elements (i.e., nodes, compound nodes and edges).
 * Cytoscape follows CSS conventions and most of the properties' name and behaviour are similar.
 */
@Injectable()
export class GraphStylesService {

   private static readonly FONT_SIZE: number = GraphConstants.FONT_SIZE;
   private static readonly FONT_FAMILY: string = StringUtil.FONT_FAMILY;

   private static readonly NODE_PADDING: string = `${GraphConstants.NODE_PADDING}px`;
   private static readonly ROOT_NODE_PADDING: string = "10px";
   private static readonly COMPOUND_NODE_PADDING: string = "40px";
   private static readonly LINE_WIDTH: number = 1;
   private static readonly LINE_DANGER_WIDTH: number = 1.5;
   private static readonly OVERLAY_OPACITY: number = 0; // Disables :active state of nodes and edges

   private static readonly ICON_POS_Y: number = 10;
   private static readonly ICON_POS_X: number = 10;

   constructor(private graphThemeService: GraphThemeService) {
   }

   getGraphStyles(): any {
      const elementsStyles: GraphElementStyles = this.getGraphElementsStyles();
      const graphStyles: any[] = [
         {
            // Nodes styling:
            selector: "node",
            css: elementsStyles.node,
         },
         {
            // Node hover state styling:
            selector: `node.${GraphClasses.NODE_HOVERED()}`,
            css: elementsStyles.nodeHover,
         },
         {
            // Root node styling:
            selector: `node.${GraphClasses.ROOT_NODE()}`,
            css: elementsStyles.nodeRoot,
         },
         {
            // Compound nodes styling:
            selector: ":parent",
            css: elementsStyles.compoundNode,
         },
         {
            // Edges styling:
            selector: "edge",
            css: elementsStyles.edge,
         },
         {
            // Edges with high latency styling:
            selector: `edge.${GraphClasses.DANGER()}`,
            css: elementsStyles.edgeDanger,
         },
         {
            // Edges with no latency styling:
            selector: `edge.${GraphClasses.DUMMY()}`,
            css: elementsStyles.edgeDummy,
         },
      ];
      return graphStyles;
   }

   private getGraphElementsStyles(): GraphElementStyles {
      const layoutStyles: GraphElementStyles = this.getElementsLayoutStyles();
      const themeStyles: GraphElementStyles =
            this.graphThemeService.getThemeStyles(GraphThemeService.IO_DIAGNOSTICS_GRAPH);
      return VsanUiUtils.deepmergeObjects(layoutStyles, themeStyles);
   }

   private getElementsLayoutStyles(): GraphElementStyles {
      return {
         node: {
            "label": (node) => node.data().label ? node.data().label : node.data().id,
            "text-valign": "center",
            "text-halign": "center",
            "text-wrap": "wrap",
            "border-width": GraphStylesService.LINE_WIDTH,
            "width": "label",
            "height": "label",
            "shape": "rectangle",
            "padding": GraphStylesService.NODE_PADDING,
            "font-family": GraphStylesService.FONT_FAMILY,
            "font-size": GraphStylesService.FONT_SIZE,
            "overlay-opacity": GraphStylesService.OVERLAY_OPACITY,
         },
         nodeRoot: {
            "padding": GraphStylesService.ROOT_NODE_PADDING,
         },
         compoundNode: {
            // Define dimensions for host icon and host name
            "background-width": [GraphConstants.ICON_SIZE, "auto", "auto"],
            "background-height": [GraphConstants.ICON_SIZE, "auto", "auto"],
            // Position host icon and host name
            "background-position-y": (compoundNode) => SvgUtil.getParentNodeImagesYpos(
                  compoundNode,
                  GraphStylesService.ICON_POS_Y,
                  GraphConstants.ICON_SIZE),
            "background-position-x": (compoundNode) => this.getDimensionsX(compoundNode).positions,
            // Min width based on images width
            "min-width": (compoundNode) => this.getDimensionsX(compoundNode).getFullWidth(),
            "min-width-bias-right": "100%",
            // Remove the default label of the compound node
            "label": "",
            "border-width": GraphStylesService.LINE_WIDTH,
            "padding": GraphStylesService.COMPOUND_NODE_PADDING,
         },
         edge: {
            "curve-style": "unbundled-bezier",
            "width": GraphStylesService.LINE_WIDTH,
            "target-arrow-shape": "triangle",
            "overlay-opacity": GraphStylesService.OVERLAY_OPACITY,
         },
         edgeDanger: {
            "width": GraphStylesService.LINE_DANGER_WIDTH,
         },
      };
   }

   private getDimensionsX(compoundNode: any): ImageDimensionsX {
      return SvgUtil.getParentNodeImagesDimensionsX(
            compoundNode,
            GraphStylesService.ICON_POS_X,
            GraphConstants.ICON_SIZE,
            GraphConstants.NODE_IMAGE_LEFT_MARGIN);
   }
}
