import {GraphElements} from "@generated/graph-elements";
import {VsanIoLatency} from "@generated/vsan-io-latency";
import {VsanIoLatencyType} from "@generated/vsan-io-latency-type";
import {ArrayUtils} from "@util/array-util";

export class VirtualDiskLatency {
   constructor(
         /**
          * VMDK uuid
          */
         public id: string,
         /**
          * VMDK name
          */
         public name: string,
         public domComponentUuid: string,
         public type: VsanIoLatencyType,
         public typeLabel: string,
         public avgRead: number,
         public stddevRead: number,
         public avgWrite: number,
         public stddevWrite: number) {
   }

   static fromGraphElementsList(graphElementsList: GraphElements[]): VirtualDiskLatency[] {
      const result: VirtualDiskLatency[] = ArrayUtils.flatten(graphElementsList.map(VirtualDiskLatency.fromGraphElements));
      return result;
   }

   private static fromGraphElements(graphElements: GraphElements): VirtualDiskLatency[] {
      const result: VirtualDiskLatency[] = ArrayUtils.flatten(graphElements.edges.map(edge =>
            VirtualDiskLatency.fromVsanIoLatencies(edge.data.latenciesStats, graphElements.id, graphElements.name)));
      return result;
   }

   static fromVsanIoLatencies(latencies: VsanIoLatency[], id?: string, name?: string): VirtualDiskLatency[] {
      return latencies.map(latency => VirtualDiskLatency.fromVsanIoLatency(latency, id, name));
   }

   static fromVsanIoLatency(latency: VsanIoLatency, id?: string, name?: string): VirtualDiskLatency {
      return new VirtualDiskLatency(
            id,
            name,
            latency?.detailedInfo.domComponentUuid,
            latency?.latencyType,
            latency?.latencyLabel,
            latency?.readLatencyStats?.averageLatency,
            latency?.readLatencyStats?.stddevLatency,
            latency?.writeLatencyStats?.averageLatency,
            latency?.writeLatencyStats?.stddevLatency);
   }
}
