import {VsanUiUtils} from "@util/vsan-util";

/**
 * A base class that is used to manage items that implement IDestroyable interface
 */
export class DestroyableInstance implements IDestroyable {
   instances: IDestroyable[] = [];
   destroy(): void {
      if (VsanUiUtils.isEmpty(this.instances)) {
         return;
      }
      this.instances.forEach(i => i?.destroy());
      this.instances = [];
   }
   addDestroyable(instance: IDestroyable): void {
      this.instances.push(instance);
   }
}

export interface IDestroyable {
   destroy: () => void;
}