import {Component, Input} from "@angular/core";
import {ModalBuilder} from "@util/modal-builder";
import {VsanRoutes} from "../../../../vsan-routes";
import {LatencyExportModalContextData} from "../dialog/latency-export-modal-context.data";
import {VirtualDiskLatency} from "../model/virtual-disk-latency";
import cytoscape from "cytoscape";
import {ZipDownloader} from "@component/export/downloaders/zip-downloader";
import {LatencyExportConfig} from "../dialog/latency-export-config";
import {GraphElements} from "@generated/graph-elements";
import {VirtualDiskIdentifier} from "../model/virtual-disk-identifier";
import {GraphViewService} from "../graph-view/graph-view.service";
import {IoDiagnosticsUtil} from "../io-diagnostics.util";

@Component({
   templateUrl: "io-diagnostics-export-action.html",
   styleUrls: ["io-diagnostics-export-action.scss"],
   selector: "vsan-io-diagnostics-export-action",
})
export class IoDiagnosticsExportActionComponent {

   private readonly DEFAULT_ZIP_NAME: string = "io_trip_analyzer";

   @Input()
   vmdksLatency: VirtualDiskLatency[];

   private _graphElements: GraphElements[];

   @Input()
   get graphElements(): GraphElements[] {
      return this._graphElements;
   }

   set graphElements(value: GraphElements[]) {
      this._graphElements = value;
      if (!value?.length) {
         return;
      }
      // Filter items that have stats only
      this.vmdksIdsWithStats = value.filter(IoDiagnosticsUtil.canRenderGraph)
            .map(VirtualDiskIdentifier.fromGraphElements);
   }

   @Input()
   selectedVmdkId: string;

   vmdksIdsWithStats: VirtualDiskIdentifier[] = [];

   constructor(private graphViewService: GraphViewService) {
   }

   isExportButtonVisible(): boolean {
      return !!this.vmdksIdsWithStats?.length;
   }

   selectVMDKs(): void {
      const context: LatencyExportModalContextData = {
         vmdksIdentifiers: this.vmdksIdsWithStats,
      };
      new ModalBuilder(`${VsanRoutes.VM_IO_DIAGNOSTICS_DIALOG}/latencyExport`)
            .setSize(800, 400)
            .open(context)
            .then(this.exportSelectedVmdks);
   }

   private exportSelectedVmdks = (vmdksIds: string[]): void => {
      const latencyData: VirtualDiskLatency[] = this.vmdksLatency.filter(vmdkLatency =>
            vmdksIds.includes(vmdkLatency.id));
      const zipDownloader: ZipDownloader = (new ZipDownloader())
            .addCsv(LatencyExportConfig.vmdkLatencyFileName, latencyData, LatencyExportConfig.getLatencyDataExtractors());
      const graphPNGs: GraphImageExport[] = this.getGraphPngs(vmdksIds);
      graphPNGs.forEach((png, index) => zipDownloader.addPng(`graph_${png.name}`, png.content));
      zipDownloader.download(this.DEFAULT_ZIP_NAME);
   };

   private getGraphPngs(vmdksIds: string[]): GraphImageExport[] {
      const result: GraphImageExport[] = [];
      this.graphElements.filter(graphEles => vmdksIds.includes(graphEles.id))
            .forEach((graphElements, index) => {
               const vmdkName: string = IoDiagnosticsUtil.getVmdkName(graphElements.name);
               // Export the rendered graph
               if (graphElements.id === this.selectedVmdkId) {
                  result.push(new GraphImageExport(vmdkName, this.getGraphPng(this.graphViewService.graph)));
               } else {
                  // Render graphs in a hidden element in order to export them and then destroy them
                  const graphContainer: HTMLDivElement = this.createGraphContainer(index);
                  const graph: cytoscape.Core = this.graphViewService.initCytoscapeBase(graphContainer, graphElements);
                  result.push(new GraphImageExport(vmdkName, this.getGraphPng(graph)));
                  this.graphViewService.destroyCytoscapeBase(graph);
                  document.body.removeChild(graphContainer);
         }
      });
      return result;
   }

   private createGraphContainer(idSuffix: number): HTMLDivElement {
      const element: HTMLDivElement = document.createElement("div");
      element.id = `graph_export_${idSuffix}`;
      const styles: string = "visibility: hidden; width: 100%; height: 100%; position: absolute; left: 0; top: 0;";
      element.setAttribute("style", styles);
      document.body.appendChild(element);
      return element;
   }

   private getGraphPng(graph: cytoscape.Core): string {
      return graph.png({output: "base64uri"}).replace(/^data:image\/(png|jpg);base64,/, "");
   }
}

class GraphImageExport {
   constructor(public name: string, public content: string) {
   }
}