import {VsanUiUtils} from "@util/vsan-util";
import {DestroyableInstance} from "./destroyable-instance";

/**
 * A base class that is used to manage popper instances
 */
export class PopperInstance extends DestroyableInstance {
   destroy(): void {
      if (VsanUiUtils.isEmpty(this.instances)) {
         return;
      }
      this.instances.forEach(i => {
         i?.destroy(); // Destroys popper instance
         (i as any)?.state?.elements?.popper?.remove(); // Destroys DOM element
      });
      this.instances = [];
   }
}