import {ElementRef, Injectable} from "@angular/core";
import {GraphElements} from "@generated/graph-elements";
import {GraphNode} from "@generated/graph-node";
import {ManagedObject} from "@service/managed-object";
import cytoscape, {CytoscapeOptions, EdgeDefinition, ElementDefinition, ElementsDefinition} from "cytoscape";
import dagre from "cytoscape-dagre";
import popper from "cytoscape-popper";
import {Observable, Subject} from "rxjs";
import {GraphConstants} from "./cytoscape-properties/graph-constants";
import {GraphStylesService} from "./cytoscape-properties/graph-styles.service";
import {GraphAccessibilityService} from "./graph-accessibility.service";
import {GraphDetailsService} from "./graph-details.service";
import {GraphLayoutService} from "./graph-layout.service";
import {GraphLayoutSpec} from "./model/graph-layout-spec";
import {GraphNodeDetails} from "./model/graph-node-details";

cytoscape.use(dagre);
cytoscape.use(popper);

/**
 * Service that manages Cytoscape instance (i.e., creates and destroys it).
 */
@Injectable()
export class GraphViewService {

   private static GRAPH_REF_FIELD: string = "vsanIoGraph";
   private static GRAPH_LAYOUT_SPEC: GraphLayoutSpec = {
      runDagre: true,
      horizontallyAlignDomOwnerStorageNodes: true,
      offsetOverlappingCompoundNodes: true,
      leftCenterNodes: true,
      createBezierCurves: true,
      resizeViewport: true,
   };
   private static CYTOSCAPE_OPTIONS: CytoscapeOptions = {
      userZoomingEnabled: false,
      userPanningEnabled: false,
      boxSelectionEnabled: false,
      autoungrabify: true,
   };
   graph: cytoscape.Core;
   private graphContainer: HTMLElement | null;

   /**
    * The Subject that allows others to subscribe notice when graph node is clicked
    */
   private graphNodeClicked: Subject<GraphNodeDetails> = new Subject<GraphNodeDetails>();

   /**
    * The Observable exposed to the public.
    */
   onGraphNodeClicked: Observable<GraphNodeDetails> = this.graphNodeClicked.asObservable();

   constructor(private layoutService: GraphLayoutService,
         private detailsService: GraphDetailsService,
         private graphStylesService: GraphStylesService,
         private accessibilityService: GraphAccessibilityService) {
   }

   /**
    * A method that creates the main graph plus adding support for dialogs and accessibility
    */
   initCytoscape(graphContainer: ElementRef, elements: GraphElements, clsRef: ManagedObject): void {
      // Destroy existing details points instances, for example if graph is reinit.
      this.destroyCytoscapePlugins();
      this.graphContainer = graphContainer.nativeElement;
      this.graph = this.initCytoscapeBase(this.graphContainer, elements);
      // Graph reference is needed by QEs.
      window[GraphViewService.GRAPH_REF_FIELD] = this.graph;
      this.handleEvents();

      this.detailsService.addDetails(this.graph, clsRef);
      this.accessibilityService.enableAccessibility(this.graph);
   }

   /**
    * A method that creates the main graph. It's useful for exporting when no additional addons are required.
    */
   initCytoscapeBase(containerElement: HTMLElement, elements: GraphElements): cytoscape.Core {
      const graphStyles: any = this.graphStylesService.getGraphStyles();
      const graph: cytoscape.Core = cytoscape(Object.assign({
         container: containerElement,
         style: graphStyles,
      }, GraphViewService.CYTOSCAPE_OPTIONS));
      graph.add(this.getElementsDefinition(elements));
      this.layoutService.run(graph, GraphViewService.GRAPH_LAYOUT_SPEC);
      return graph;
   }

   private getElementsDefinition(elements: GraphElements): ElementsDefinition {
      const elementsDefinition: ElementsDefinition = {
         nodes: elements.nodes as unknown as ElementDefinition[],
         edges: elements.edges as unknown as EdgeDefinition[],
      };
      return elementsDefinition;
   }

   private handleEvents(): void {
      // Attach to graph via node selector instead of graph elements as otherwise event is fired twice.
      this.graph.on(GraphConstants.GRAPH_TAP_EVENT, "node", (event) => {
         const clickedNode: GraphNode =
               this.graph.nodes().filter(node => node.id() === event.target.id()).first() as unknown as GraphNode;
         if (clickedNode) {
            this.graphNodeClicked.next(GraphNodeDetails.fromGraphNode(clickedNode));
         }
      });
   }

   /**
    * A method that destroys the main graph including addons for accessibility and dialogs
    */
   destroyCytoscape(): void {
      this.destroyCytoscapePlugins();
      const isDestroyed: boolean = this.destroyCytoscapeBase(this.graph);
      if (isDestroyed) {
         delete window[GraphViewService.GRAPH_REF_FIELD];
      }
   }

   /**
    * A method that destroys the main graph only.
    */
   destroyCytoscapeBase(graph: cytoscape.Core): boolean {
      if (graph && !graph.destroyed()) {
         graph.destroy();
         return true;
      }
      return false;
   }

   private destroyCytoscapePlugins(): void {
      this.detailsService.destroy();
      this.accessibilityService.destroy();
   }
}