import {Injectable} from "@angular/core";
import {GraphClasses} from "@generated/graph-classes";
import {DomUtil} from "@util/dom-util";
import {VsanUiUtils} from "@util/vsan-util";
import cytoscape, {SingularElementArgument} from "cytoscape";
import {IoDiagnosticsUtil} from "../io-diagnostics.util";
import {GraphConstants} from "./cytoscape-properties/graph-constants";
import {IDestroyable} from "./model/destroyable-instance";
import {GraphNodeDetails, GraphNodeType} from "./model/graph-node-details";
import {PopperInstance} from "./model/popper-instance";

/**
 * This service handles accessibility of cytoscape graph. It creates HTML elements over nodes,
 * so that they can be navigated via keyboard.
 */
@Injectable()
export class GraphAccessibilityService extends PopperInstance {
   private static readonly ACCESSIBLE_NODE_PREFIX: string = "graph-accessible-node-";
   // Add class to the accessible element in order to style it's focus state via CSS.
   private static readonly ACCESSIBLE_NODE_CLASS: string = "graph-accessible-node";
   private static readonly ACCESSIBLE_NODES_CLASSES: string[] =
         [GraphClasses.STORAGE_NODE(), GraphClasses.NIC_NODE()];

   enableAccessibility(graph: cytoscape.Core): void {
      const accessibleNodes: SingularElementArgument[] = this.getAccessibleNodes(graph);
      for (const node of accessibleNodes) {
         this.createAccessibleNode(node);
         const element: HTMLElement = this.getElement(node);
         // When accessible element is hovered/hovered out change the background color of actual node via class.
         DomUtil.onMouseOver(element, () => node.addClass(GraphClasses.NODE_HOVERED()));
         DomUtil.onMouseOut(element, () => node.removeClass(GraphClasses.NODE_HOVERED()));
         // When accessible elements is tapped, tap the actual node
         const nodeTap: () => void = () => node.trigger(GraphConstants.GRAPH_TAP_EVENT);
         IoDiagnosticsUtil.attachUserEvents(element, nodeTap);
      }
   }

   private getAccessibleNodes(graph: cytoscape.Core): SingularElementArgument[] {
      return graph.nodes()
            .filter(node => GraphAccessibilityService.ACCESSIBLE_NODES_CLASSES
                  .some(nodeClass => node.hasClass(nodeClass)))
            .toArray();
   }

   private getElement(node: SingularElementArgument): HTMLElement {
      const elementId: string = GraphAccessibilityService.ACCESSIBLE_NODE_PREFIX + node.data().id;
      return document.getElementById(elementId);
   }

   /**
    * Render accessible HTML element over the actual node. Both elements should have the same position and dimensions:
    * 1) The size of both elements is based on the label itself including side padding.
    * 2) Accessible element is positioned via renderedPosition
    */
   private createAccessibleNode(node: any): void {
      const bbox: any = node.renderedBoundingBox();
      const xOffset: number = GraphConstants.NODE_PADDING + 1;
      const popperNode: IDestroyable = node.popper({
         content: () => this.createContentElement(node),
         renderedPosition: () => ({
            x: bbox.x1 + xOffset,
            y: bbox.y1 - bbox.h / 2 + 2,
         }),
         popper: {
            removeOnDestroy: true,
         },
      });
      this.addDestroyable(popperNode);
   }

   /**
    * This is the actual HTML element used by popper.js
    */
   private createContentElement(node: any): HTMLElement {
      const el: HTMLElement = IoDiagnosticsUtil.createDivElement();
      const nodeDetails: GraphNodeDetails = GraphNodeDetails.fromGraphNode(node);
      const hostName: string = IoDiagnosticsUtil.getHostName(nodeDetails);
      if (nodeDetails.type === GraphNodeType.NIC) {
         el.title = VsanUiUtils.getString(
               "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.nicNode.title",
               hostName);
         // TODO: (REPA) (vasilevd) When entityName is missing for disk/disk group use different title
      } else if (nodeDetails.type === GraphNodeType.DISK_GROUP) {
         el.title = VsanUiUtils.getString(
               "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.diskGroupNode.title",
               nodeDetails.entityName,
               hostName);
      } else if (nodeDetails.type === GraphNodeType.DISK) {
         el.title = VsanUiUtils.getString(
               "vsan.monitor.vm.iodiagnostics.instance.stats.virtualdisk.diskNode.title",
               nodeDetails.entityName,
               hostName);
      }
      el.id = GraphAccessibilityService.ACCESSIBLE_NODE_PREFIX + node.data().id;
      el.classList.add(GraphAccessibilityService.ACCESSIBLE_NODE_CLASS);
      el.tabIndex = 0;
      el.innerText = node.data().label;
      el.setAttribute("role", "button");
      document.body.appendChild(el);
      return el;
   }
}